<template>
	<div style="height: 100%;">
		<el-row style="top: 0;left: 0;bottom: 0;right: 0; margin:auto; width: 100%;height: 58%">
			<el-col :span="6" style="top: 0;left: 0;bottom: 0;right: 0; margin:auto; width: 100%;height: 100%">
				<div id="chart-RAM" style="top: 0;left: 0;bottom: 0;right: 0; margin:auto; width: 100%;height: 100%">
				</div>
			</el-col>
		</el-row>
		<el-row style="top: 0;left: 0;bottom: 0;right: 0; margin:auto; width: 100%;height: 4%;margin-top: 0%;">

		</el-row>
		<!-- :gutter 属性来指定每一栏之间的间隔，默认间隔为 0。 -->
		<el-row style="top: 0;left: 0;bottom: 0;right: 0; margin:auto; width: 100%;height: 38%;margin-top: 0%;">
			<el-col :span="6" style="top: 0;bottom: 0;left: 0;right: 0; margin:auto; width: 50%;height: 100%">
				<div id="chart-MemoryTotal" class="grid-content bg-purple" style="top: 0;left: 0;bottom: 0;right: 0; margin:auto; width: 100%;height: 100%">
				</div>
			</el-col>
			<el-col :span="6" style="top: 0;left: 0;bottom: 0;right: 0; margin:auto; width: 50%;height: 100%">
				<div id="chart-DiskUsage" class="grid-content bg-purple" style="top: 0;left: 0;bottom: 0;right: 0; margin:auto; width: 100%;height: 100%">
				</div>
			</el-col>
		</el-row>


	</div>
</template>

<script>
	// 引用axios
	import axios from 'axios'


	export default {
		name: 'echarts',
		data() {
			return {}
		},
		mounted() {
			// this.demo
			this.drawMemoryLine(),
				this.drawMemoryPie(),
				this.drawDiskPie()
		},
		methods: {
			drawMemoryLine() {
				/*模板元数据
				var data = [
					["2000-06-05", 116],
					["2000-06-06", 129],
					["2000-06-07", 135],
						
				];
				var dateList = data.map(function(item) {
					return item[0];
				});
				var valueList = data.map(function(item) {
					return item[1];
				});*/

				// X/Y轴的数据
				var dateList = [0];
				var valueList = [0];
				for (var i = 1; i < 20; i++) {
					// 初始化不赋值
					dateList.push(0);
					valueList.push(0);
				}




				// 基于准备好的dom，初始化echarts实例
				let myChart = this.$echarts.init(document.getElementById('chart-RAM'))

				var option = {

					// Make gradient line here

					//调整图表大小
					// grid: {
					// 	x: '17%',
					// 	y: '7%',
					// 	width: '100%',
					// 	height: '100%'
					// },

					grid: {
						bottom: '8%',
						width: '80%',
						height: '80%'
					},

					visualMap: {
						show: true,
						type: 'continuous',
						// 从什么颜色程度开始
						seriesIndex: 0,
						min: 0,
						max: 100
					},

					title: {
						left: 'center',
						text: 'Random Acess Memory Usage of Computer'
					},
					tooltip: {
						trigger: 'axis'
					},
					xAxis: {
						data: dateList
					},
					yAxis: {
						splitLine: {
							//分割恒纹
							show: false
						}
					},
					series: [{
						type: 'line',
						//数据描点
						showSymbol: false,
						data: valueList
					}]
				};

				// dymatic
				setInterval(function() {
					// X轴当前时间更新
					var axisData = (new Date()).toLocaleTimeString().replace(/^\D*/, '');
					option.xAxis.data.shift();
					option.xAxis.data.push(axisData);

					// 动态数据更新处
					var data0 = option.series[0].data;
					/* 					// 静态value值
										data0.shift();
										data0.push(Math.round(Math.random() * 100));
					 */

					// 发ajax请求，用以获取数据，

					axios({
						url: "/getMemoryUsage",
						method: "get",
						params: {}
					}).then(function(result) {
						// result是返回请求
						var MemoryUsage = result.data.usageMemory
						//console.log(MemoryUsage)
						data0.shift();
						data0.push(MemoryUsage);
					}).catch(function(error) {
						alert(error);
					})

					myChart.setOption(option);
				}, 2100);
			},


			// 绘制内存使用情况图
			drawMemoryPie() {

				// 基于准备好的dom，初始化echarts实例
				let myChart = this.$echarts.init(document.getElementById('chart-MemoryTotal'))

				var option = {
					//透明
					backgroundColor: 'rgba(255,255,255,.5)',

					title: {
						text: 'MemoryUsage Pie',
						left: 'center',
						top: 10,
						textStyle: {
							color: '#ccc'
						}
					},

					tooltip: {
						trigger: 'item',
						formatter: '{a} <br/>{b} : {c} ({d}%)'
					},

					/* //取消渐变特效
											visualMap: {
											show: false,
											min: 0,
											max: 999999999999,
											// 色彩饱和度
											inRange: {
												colorLightness: [0, 0.5]
											}
										}, */
					series: [{
						name: '内存使用情况',
						type: 'pie',
						radius: '55%',
						center: ['50%', '50%'],
						data: [{
								value: 0,
								name: '已使用内存',
								itemStyle: {
									//主题变化颜色设置
									color: '#d76662'
								}
							},
							{
								value: 0,
								name: '未使用内存',
								itemStyle: {
									//主题变化颜色设置
									color: '#f8ebd8'
								}
							}
						].sort(function(a, b) {
							return a.value - b.value;
						}),
						roseType: 'radius',
						//饼状图里面的数据说明设置
						label: {
							//color: 'rgba(255, 255, 255, 0.3)'
						},
						labelLine: {
							lineStyle: {
								//	color: 'rgba(255, 255, 255, 0.3)'
							},
							smooth: 0.2,
							length: 10,
							length2: 20
						},
						//阴影设置
						itemStyle: {
							//主题变化颜色设置
							//	color: '#c23531',
							//shadowBlur: 200,
							//shadowColor: 'rgba(0, 0, 0, 0.5)'
						},

						animationType: 'scale',
						animationEasing: 'elasticOut',
						animationDelay: function(idx) {
							return Math.random() * 200;
						}
					}]
				};

				//绘制图表
				myChart.setOption(option);

				// dymatic
				// setInterval(function() {
				// 	/* 		option.series[0].data[0].value = (Math.random() * 10000000000).toFixed(2) - 0;
				// 			option.series[0].data[1].value = option.series[0].data[0].value;
				// 			myChart.setOption(option, true); */


				// 发ajax请求，用以获取数据，
				axios({
					url: "/getMemoryUsage",
					method: "get",
					params: {}
				}).then(function(result) {
					// result是返回请求
					var totalMemory = result.data.totalMemory;
					var freeMemory = result.data.freeMemory;
					var allocatedMemory = result.data.allocatedMemory;
					var usageMemory = result.data.usageMemory;

					option.series[0].data[0].value = freeMemory;
					option.series[0].data[1].value = allocatedMemory;
				}).catch(function(error) {
					alert(error.data);
				})

				myChart.setOption(option);
				// }, 2100);

			},



			// 绘制磁盘使用情况图
			drawDiskPie() {

				// 基于准备好的dom，初始化echarts实例
				let myChart = this.$echarts.init(document.getElementById('chart-DiskUsage'))

				var option = {
					backgroundColor: 'rgba(255,255,255,.5)',
					title: {
						text: '磁盘使用分布情况'
					},
					tooltip: {
						trigger: 'axis'
					},
					legend: {
						top: '20%',
						left: 'center',
						data: ['boot', 'run/user/', 'sys/fs/cgroup', 'dev/shm', '总共使用']
					},
					radar: {
						indicator: [{
								text: '总共使用',
								max: 100
							},
							{
								text: '/boot',
								max: 100
							},
							{
								text: '/run',
								max: 100
							},
						/* 	{
								text: '/run/user/0',
								max: 100
							}, */
							{
								text: '/sys/fs/	cgroup',
								max: 100
							},
							{
								text:'/dev',
								max: 100
							},
							{
								text:'/dev/shm',
								max: 100
							}
						],
						center: ['25%', '40%'],
						radius: 80
					},
					series: [{
						type: 'radar',
						tooltip: {
							trigger: 'item'
						},
						areaStyle: {},
						data: [{
							value: [60, 73, 85,  15,65,100],
							name: '主机磁盘使用占比情况'
						}]
					}]

				};


				//绘制图表
				myChart.setOption(option);

				//dymatic
				setInterval(function() {
					/* option.series[0].data[0].value = [(Math.random() * 100).toFixed(2), (Math.random() * 100).toFixed(2), (Math.random() *
						100).toFixed(2), (Math.random() * 100).toFixed(2), (Math.random() * 100).toFixed(2), (Math.random() * 100).toFixed(
						2)]
					myChart.setOption(option, true); */
					// 发ajax请求，用以获取数据，

					axios({
						url: "/getDiskInfo",
						method: "get",
						params: {}
					}).then(function(result) {
						// result是返回请求
						var total = result.data.total.map();
							alert(total);
						var boot = result.data.boot.map();
						var run = result.data.run.map();
						//var user0 = result.data.0;
						var sys_fs_cgroup = result.data.cgroup.map();
						var dev = result.data.dev.map();
						var dev_shm = result.data.shm.map();
						

						option.series[0].data[0].value = [total,boot,run,user0,sys_fs_cgroup,dev,dev_shm]

						console.log(result)

					}).catch(function(error) {
						alert(error.data);
					})

					myChart.setOption(option);
				}, 2100);
			}


		}
	}
</script>

<style>
</style>
